.MainMenu {
  flex-grow:1;
  display: flex;
  gap: 16px;
  align-items: stretch;
  flex-direction: row;
}

.groupItems {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.grow {
  flex-grow: 1;
}

.spacer {
  flex-grow: 1;
}

.spacer.vertical {
  border-top: 1px solid var(--color-disabled);
}

.vertical {
  flex-direction: column;
  margin-left: 0;
}

button.mainMenuButton {
  width: 40px;
  height: 40px;
  border: solid 1px white;
  padding: 12px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menuButtonIcon {
  fill: none;
}

.menuButtonIcon path {
  fill: var(--color);
}

.mainMenuButton:hover path {
  fill: var(--color-hover);
}

.menuClosed {
  display: none;
}
