@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');
body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  --color: #0b0b0b; 
  --color-hover: #D8A041;
  --color-active: #9D9D9D;
  --color-disabled: #9D9D9D5a;
  --color-disabled-hover: #9D9D9Da5;
  --border-color: #0b0b0b;
  --border-color-hover: #D8A041;
  --bordered-border-color: #D8A041;
  --bordered-border-color-hover: white;

  --border-color-disabled: #9D9D9D5a;
  --bg-color: transparent;
  --bg-color-primary: linear-gradient(
    266.19deg, 
    rgba(255, 255, 255, 0.2) 11.25%, 
    rgba(255, 255, 255, 0.0157739) 71.46%, 
    rgba(0, 0, 0, 0) 76.61%
  ), #D8A041;
  --bg-color-primary-hover: linear-gradient(
    229.46deg, 
    rgba(255, 255, 255, 0.35) 25.14%, 
    rgba(255, 255, 255, 0.0276044) 111.72%, 
    rgba(0, 0, 0, 0) 119.13%
  ), #D8A041;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 28px;
}

@media (max-width: 1220px) { 
  h1 {
    font-size: 28px;
  }  
  
  h2 {
    font-size: 22px;
  }
}
