.Button {
  display: block;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  border: 1px solid var(--color-active);
  color: var(--color-active);
  padding: 8px;
  height: 48px;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
}

.Button:hover {
  border: 1px solid var(--color-hover);
  color: var(--color-hover);
}

.wide {
  padding: 8px 16px;
}

a, .link {
  padding: 8px 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  text-decoration: none;
  display: pointer;
  color: var(--color);
}

a:hover, .link:hover {
  color: var(--color-hover);
}

a.active:hover, .link.active {
  color: var(--color-active);
}

a.disable:hover, .link.disable:hover {
  color: var(--color-disabled-hover);
}

.primary {
  padding: 8px 16px;
  border: none;
  color: white;
  background: var(--bg-color-primary);
} 

.primary:hover {
  border: none;
  color: white;
  background: var(--bg-color-primary-hover);
}

.bordered {
  height: auto;
  color: var(--color);
  font-size: 22px;
  border-radius: 0;
  padding: 18px 35px;
  border: 6px double var(--bordered-border-color);
  outline: 1px solid var(--bordered-border-color);
}

.bordered:hover {
  border: 6px double var(--bordered-border-color-hover);
  outline: 1px solid var(--bordered-border-color-hover);
  background: var(--bg-color-primary);
  color: white;
}

.disabled {
  touch-action: none;
  /* pointer-events: none; */
  cursor: progress;
  color: var(--color-disabled);
}

.menuItem {
  justify-content: space-between;
  font-weight: 600;
  font-size: 22px;
}

.menu {
  display: block;
  background-image: url('./menu-hover.png');
  background-size: cover;
  flex-grow: 0;
  flex-shrink: 0;
  width: 12px;
  height: 20px;
}

